import React, {useState, useEffect} from 'react';

import EUR from '../img/Flags/EUR.svg';
import USA from '../img/Flags/USA.svg';
import CHN from '../img/Flags/CHN.svg';

const Cotaçao  = () => {
   const [datas, setData] = useState({"USDBRL":{},"EURBRL":{},"CNYBRL":{}})

    useEffect(()=>{
        const fetchData = async ()=>{
            const res = await fetch('https://economia.awesomeapi.com.br/last/USD-BRL,EUR-BRL,CNY-BRL')
            const data = await res.json();
            setData(data)
            console.log(data)
        }
        fetchData();
    },[])
    
    //.map(data => (<div> {data.name} + {data.ask} </div>))
    return (
        
            <div className="cotaçao">
                <div id="USDBRL" className="cotaItem">
                    <img src={USA} alt='DOLAR' className="flag"/>
                    <label>R${datas.USDBRL.ask}</label>
                </div>
                <div id="EURBRL" className="cotaItem">
                    <img src={EUR} alt='EURO' className="flag"/>
                    <label>R${datas.EURBRL.ask}</label>
                </div>
                <div id="CNYBRL" className="cotaItem">
                <img src={CHN} alt='CHINA' className="flag"/>              
                    <label>R${datas.CNYBRL.ask}</label>
                </div>
            </div>

)};

export default Cotaçao;