import React from 'react';
import {Link} from 'react-router-dom'

import littleLogo from '../little-logo.jpg'

export default function NavBar() {


return (
    <nav >
        <div id="nav-space" className="navbar-expand-lg">
            <div id="nav-logo">
                <img src={littleLogo} alt="little-logo"></img>
            </div> 
            
            <div className="nav-links">
                    <ul >
                        <li>
                            <Link to="/">Agenda</Link>
                        </li>
                        <li>
                            <Link to="/teste">Contato</Link>
                        </li>
                        <li>
                            <Link to="/admin">Sobre</Link>
                        </li>
                        
                    </ul>
            </div>
           
        </div>    
    </nav>
);
}
