import React, {useState, useEffect} from 'react';
import AgendaItem from './AgendaItem';

const Agenda  = () => {
    
    const [data, setData] = useState([{'dia':'00', 'mes':'55', 'localiza':'Agenda', 'time':'16hrs'} ])

    useEffect(() => {
        const fetchData = async () => {
            const result = await fetch('/api/agenda/');
            const body = await result.json();
            const lista = body.listaLeilao;
           
            setData(lista); 
            
        }
        fetchData();

    },[]);

    const delLeilao = async () => {
        const result = await fetch('/api/agenda/delLeilao', {
            method: 'POST',
            //body: JSON.stringify({dia:startDate.getDate(), mes:startDate.getMonth() + 1, time:startTime, localiza: local}),
            headers: {
                'Content-Type': 'application/json',
            }
        });
        const body = await result.json();
        setData(body);                    
        console.log(body)
    }

    return (
        
        <div className='' id="agenda" >
            <h1 id="agenda-titulo">Agenda de Leilões</h1>
            <div id="agenda-items">
              
                {data.map((datas, index) =>  index < 2 && <div key={index}> <AgendaItem dia={datas.dia} mes={datas.mes} localiza={datas.localiza} time={datas.time} /> </div>)}
            </div>
            <button onClick={delLeilao}>Remove first</button>
        </div>
        
);
}

export default Agenda;