import React from 'react';
import Carousel from 'react-bootstrap/Carousel';
import SliderText from './SliderText';
import slider1 from '../img/slider1.png';
import slider2 from '../img/slider2.png';
import slider3 from '../img/slider3.png';

const Slider  = () => (
    <div id="slider">
   
      <Carousel variant="dark" fade="true" pause="hover">
        <Carousel.Item className= "carousel-item">
          <img
            className="sliderImg"
            src={slider1}
            alt="First slide"
          />

        </Carousel.Item> 
        <Carousel.Item>
          <img
            className="sliderImg"
            src={slider2}
            alt="Second slide"
          />

          <Carousel.Caption>
            <SliderText text='try this shit'/>
          </Carousel.Caption>
        </Carousel.Item>
         <Carousel.Item>
          <img
            className="sliderImg"
            src={slider3}
            alt="Third slide"
          />

          <Carousel.Caption>
            <h3>Third slide label</h3>
            <p>Praesent commodo cursus magna, vel scelerisque nisl consectetur.</p>
          </Carousel.Caption>
        </Carousel.Item>
        <Carousel.Item>
          <img
            className="sliderImg"
            src={slider3}
            alt="Thirdxxxx slide"
          />

          <Carousel.Caption>
            <h3>Third slide label</h3>
            <p>Praesent commodo cursus magna, vel scelerisque nisl consectetur.</p>
          </Carousel.Caption>
        </Carousel.Item>
      </Carousel>
    </div>
);

export default Slider;
