import React from 'react';

const AgendaItem  = ({dia, mes, localiza, time}) => {

    let recinto, diaDisplay, mesDisplay;
    
    
    if(dia.toString().length < 2){
        diaDisplay = '0' + dia.toString();
    }else{
        diaDisplay = dia;
        
    }
    if(mes.toString().length < 2){
        mesDisplay = '0' + mes.toString();
    }else{
        
        mesDisplay = mes;
    }
    switch(localiza){
        case 'Londrina-PR':
            recinto = 'Pq. Gov. Ney Braga'
        break;
        case 'Sapopema-PR':
            recinto = 'Salto das Orquídeas'
        break;
        case 'Curiúva-PR':
            recinto = 'Não sei esse'
        break;
        default:
            recinto = 'Sem Recinto';
    }
        return (   
        <div id="caixa" className="">
            <div className="diaMes" >
                <h3 id='dia' className="diaMes">{diaDisplay}</h3>
                <h3 id='mes' className="diaMes">{mesDisplay}</h3>
            </div>
            <div id='' className="conteudo">
                <h3 id='localiza'>{localiza}</h3>
                <h4 id='time'>Às {time}</h4>
                <p>Recinto:</p>
                <h4>{recinto}</h4>
                <p>End.: Rua Tal e tal</p>
            </div>
        </div> 
);}

export default AgendaItem;