import React, { useState } from "react";
import DatePicker from "react-datepicker";
import AgendaItem from '../components/AgendaItem';

import "react-datepicker/dist/react-datepicker.css";

const AdminPage = () => {

    const [startDate, setStartDate] = useState(new Date());
    const [startTime, setStartTime] = useState(" Horário ");

    const [local, setLocal] = useState(" Local/Recinto ");

    //const [itemInfo, setItemInfo] = useState({dia:startDate.getDate(), mes:startDate.getMonth() + 1, time:startTime, localiza: local});

    const addLeilao = async () => {
        let addId = 0;
        const result = await fetch('/api/agenda/addLeilao', {
            method: 'POST',
            body: JSON.stringify({id: addId++, dia:startDate.getDate(), mes:startDate.getMonth() + 1, time:startTime, localiza: local}),
            headers: {
                'Content-Type': 'application/json',
            }
        });
        const body = await result.json();                       
        console.log(body)
    }
    return (
    <>
        <div id="admin" >
            <h1>AdminPage</h1>

            <div id='selectionPane'>
                <div className="selectionTitle">
                    <h1>Editar Agenda</h1>
                </div>
                <div className="selectionContent">

                    <div id="timeDate">
                        <h3>Local:</h3>
                        <div className="dropdown">
                            <button className="dropbtn">{local}</button>
                            <div className="dropdown-content">
                                <button onClick={(e) => setLocal(e.target.value)} value='Londrina-PR'>Londrina-PR</button>
                                <button onClick={(e) => setLocal(e.target.value)} value='Sapopema-PR'>Sapopema-PR</button>
                                <button onClick={(e) => setLocal(e.target.value)} value='Curiúva-PR'>Curiúva-PR</button>
                            </div> 
                        </div>
                        <br/>
                        <br/>
                        
                        <div>
                            <h3>Horário:</h3>
                            <div className="dropdown">
                                <button className="dropbtn">{startTime}</button>
                                <div className="dropdown-content">
                                    <button onClick={(e) => setStartTime(e.target.value)} value='16hrs'>16 Horas</button>
                                    <button onClick={(e) => setStartTime(e.target.value)} value='17hrs'>17 Horas</button>
                                    <button onClick={(e) => setStartTime(e.target.value)} value='18hrs'>18 Horas</button>
                                    <button onClick={(e) => setStartTime(e.target.value)} value='19hrs'>19 Horas</button>

                                </div>
                            </div>
                            
                        </div>
                        <br/>
                        <br/>
                    <div>
                        <button className="dropbtn" onClick={(e)=>{addLeilao()}}>Adicionar</button>
                       
                    </div>
                    </div>
                    <div className="flex-two">
                        <h3>Data:</h3>
                                            
                        <DatePicker selected={startDate} onChange={(date) => setStartDate(date)} inline />

                    </div>
                    <div>
                        <h3>Resumo:</h3>
                       
                        <AgendaItem dia={startDate.getDate()} mes={startDate.getMonth() + 1} localiza={local} time={startTime} />
                    </div>
                </div>
            </div> 



        </div>
    </>
);
}
export default AdminPage;